.contact-form-box{
    background-color: #fff;
    border: 1px solid #ECECEC;
    padding: 20px;
}

.contact-form-box h4{
 font-size: 30px;
 font-weight: 600;   
 color: #080036;
}
.contact-form-box .title p{
    font-size: 16px;   
    color: #545454;
    padding-bottom: 30px;
}


.contact-us-info{
    padding: 35px 35px;
    padding-bottom: 20px;
    height: 100%;
}

.contact-info-metas h4{
    font-size: 24px;
    font-weight: 600;
    color: #080036;
}
.contact-info-metas p{
    font-size: 17px;
    color: #545454;
}

.contact-info-metas .with-icon{
    position: relative;
    padding-left: 80px;
    margin-bottom: 40px;
}
.contact-info-metas .with-icon:last-child{
    margin-bottom: 0;
}


.contact-info-metas .with-icon img{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
}

.contact-info-metas .with-icon::after{
    content: "";
    width: 1px;
    height: 36px;
    background-color: #B7CEF0;
    display: block;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 55px;
}

.contact-us-media{
    text-align: center;
    margin-top: 50px;
}


@media screen and (max-width: 1199px){
    .contact-us-media{
        margin-top: 20px;
    }

    .conatct-us .row > [class*="col-"]:first-child{
        padding-bottom: 30px;
    }

}


@media screen and (max-width: 575px){
    .contact-form-box h4{
        font-size: 24px;
    }
    .contact-us-info{
        padding: 25px 20px;
    }
    .contact-info-metas h4{
        font-size: 20px;
    }
    .contact-info-metas p{
        font-size: 16px;
    }
    .contact-us-media{
        margin-top: 40px;
    }
    .contact-info-metas .with-icon{
        margin-bottom: 30px;
    }
    .contact-info-metas .with-icon img{
        width: 18px;
    }
    .contact-info-metas .with-icon::after{
        left: 45px;
    }

}